/* FONTS */

/* CARDS */

.card {
  display: grid;
  grid-template-columns: 100px;
  grid-template-rows: 150px 150px 80px;
  grid-template-areas: "image" "text" "links";
  /* background: white; */
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.9);
  font-family: roboto;
  font-size: 20px;
  text-align: center;
  max-width: 450px;
  border-radius: 20px !important;
}

.card-image .image {
  grid-area: image;
  max-width: 300px;
  /* background: url(""); */
  background-size: contain;
  padding-top: 1rem;
}

.card-text .text {
  grid-area: text;
  margin: 1rem;
  font-size: 2rem;
}

.card-links {
  grid-area: links;
}

.card-text .date {
  color: rgb(255, 7, 110);
  font-size: 13px;
}

.card-text p {
  color: #000000;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: left;
  margin: 1rem;
}

.card-text h2 {
  margin-top: 0px;
  font-family: Barriecito, "cursive";
  font-size: 3rem;
  padding-top: 1rem;
  color: #00b3cd;
}

.card-text h3 {
  margin-top: 0px;
  font-family: Barriecito, "cursive";
  font-size: 2rem;
  color: #fea905;
  text-align: left;
  margin-left: 1rem;
}

.card-text h4 {
  margin-top: 0px;
  font-size: 28px;
}

.card-links {
  grid-area: links;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  background: #00b3cd;
  color: white;
  width: 100%;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.card-links .link {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  color: white;
  padding: 0.5rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  width: 60%;
}

.card-links .link img {
  width: 26px;
}

.card:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.6);
}

.card {
  transition: 0.5s ease;
  cursor: pointer;
  margin: 1rem;
  width: 245px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

