:root {
  --brandColor: #00b3cd;
  --background: rgb(247, 247, 247);
  --textDark: hsla(0, 0%, 0%, 0.66);
  --textLight: hsla(0, 0%, 0%, 0.33);
}

body {
  background: linear-gradient(to right top, #00b2cd7d, #feab05b5);
  min-height: 100vh;
  color: hsla(0, 0%, 0%, 0.66);
  color: var(--textDark);
}

.field:not(:last-child) {
  margin-bottom: 2rem;
}

.register {
  margin: 5rem 0;
  /* background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  ); */
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
}

/* .register::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.5;
} */

.left,
.right {
  padding: 4.5rem;
}

.left {
  border-right: 2px solid rgb(247, 247, 247);
  border-right: 2px solid var(--background);
}

.left .title {
  font-weight: 800;
  letter-spacing: -2px;
}

.left .colored {
  color: #00b3cd;
  color: var(--brandColor);
  font-weight: 500;
  margin-top: 1rem !important;
  letter-spacing: -1px;
}

.left p {
  color: hsla(0, 0%, 0%, 0.33);
  color: var(--textLight);
  font-size: 1.15rem;
}

.right .title {
  font-weight: 800;
  letter-spacing: -1px;
}

.right .description {
  margin-top: 1rem;
  margin-bottom: 1rem !important;
  color: hsla(0, 0%, 0%, 0.33);
  color: var(--textLight);
  font-size: 1.15rem;
}

.right small {
  color: hsla(0, 0%, 0%, 0.33);
  color: var(--textLight);
}

input {
  font-size: 1rem;
}

input:focus {
  border-color: #00b3cd !important;
  border-color: var(--brandColor) !important;
  box-shadow: 0 0 0 1px #00b3cd !important;
  box-shadow: 0 0 0 1px var(--brandColor) !important;
}

.fab,
.fas {
  color: hsla(0, 0%, 0%, 0.33);
  color: var(--textLight);
  margin-right: 1rem;
}

@media (max-width: 768px) {
  .left {
    border-right: none;
  }
}

